.checkboxBlock {
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  min-width: 70px;
}
@media screen and (max-width: 1000px) {
  .checkboxBlock {
    min-width: 0;
  }
}
@media screen and (max-width: 620px) {
  .checkboxBlock {
    align-self: start;
  }
}
