.actionsLayoutBlock {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.contentWrapper {
  width: 100%;
  display: flex;
  gap: 30px;
}
.contentAsideAndSection {
  display: grid;
  width: 1290px;
  /* width: auto; */
  margin: 0 auto;
  padding: 40px 0 0 0;
}
.contentSection {
  width: 100%;
}
.pageTitle {
  width: 100%;
  margin-bottom: 24px;
  display: flex;
  align-items: center;
  justify-content: flex-start;
}
.popularGood {
  /* max-width: 1290px; */
  width: 100%;
  margin: 0 auto;
}
@media screen and (max-width: 910px) {
  .responsiveWrapper {
    display: none;
  }
}
@media screen and (max-width: 1440px) {
  .contentAsideAndSection {
    width: auto;
    margin: 0 20px;
  }
}
