.MySuccessOrderBlock {
  width: 100%;
  height: 500px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.MySuccessOrderWrapper {
  background-color: var(--main-white);
  width: 580px;
  border-radius: 2px;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 15px;
}
.MySuccessOrderTitle {
  font-weight: 700;
  font-size: 24px;
  line-height: 150%;
  letter-spacing: 0.02em;
  color: #003767;
  text-align: center;
  margin-bottom: 15px;
}
.MySuccessOrderImg {
  width: auto;
  height: 175px;
}
.MySuccessOrderTitleSecond {
  font-weight: 500;
  font-size: 18px;
  line-height: 150%;
  letter-spacing: 0.02em;
  color: #003767;
  text-align: center;
  margin-top: 15px;
}
.MySuccessOrderSubText {
  font-weight: 400;
  font-size: 16px;
  line-height: 150%;
  letter-spacing: 0.02em;
  color: #003767;
  text-align: center;
}
.MySuccessOrderSubTextId {
  font-weight: 400;
  font-size: 16px;
  line-height: 150%;
  letter-spacing: 0.02em;
  color: #003767;
  text-align: center;
}
.MySuccessOrderIdSpan {
  font-weight: 700;
  font-size: 18px;
  line-height: 150%;
  letter-spacing: 0.02em;
  color: #003767;
  text-align: center;
}
@media screen and (max-width: 600px) {
  .MySuccessOrderWrapper {
    width: 100%;
  }
}
