.SimplePostImgPartBlock {
  position: relative;
  width: 100%;
  height: 100%;
  overflow: hidden;
  animation: rightShow 1s;
}

.SimplePostImgPartImg {
  width: auto;
  height: 100%;
  object-fit: cover;
}
.SimplePostImgPartTextPart {
  height: 150px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  position: absolute;
  top: 35%;
  right: 5%;
}
.SimplePostImgPartTextPartTitle {
  font-size: 18px;
  line-height: 150%;
  font-weight: 500;
  letter-spacing: 0.02em;
  color: #003767;
}
.SimplePostImgPartTextPartText {
  width: 80%;
  font-size: 14px;
  font-weight: 400;
  line-height: 150%;
  color: #003767;
}

@keyframes rightShow {
  0% {
    right: -500px;
    opacity: 0.2;
  }
  100% {
    right: 0;
    opacity: 1;
  }
}

@media screen and (max-width: 1200px) {
  .SimplePostImgPartImg {
    opacity: 0.5;
  }
}

@media screen and (max-width: 768px) {
  .SimplePostImgPartBlock {
    width: 90%;
    display: flex;
    align-items: center;
    justify-content: center;
  }
}

@media screen and (max-width: 576px) {
  .SimplePostImgPartTextPart {
    width: 45%;
    gap: 30px;
  }
  .SimplePostImgPartImg {
    height: 80%;
  }
  .SimplePostImgPartTextPartTitle {
    font-size: 16px;
  }
  .SimplePostImgPartTextPartText {
    font-size: 14px;
  }
}

@media screen and (max-width: 400px) {
  .SimplePostImgPartTextPart {
    width: 45%;
    top: 20%;
    height: auto;
    gap: 15px;
  }
  .SimplePostImgPartImg {
    height: 100%;
  }
  .SimplePostImgPartTextPartTitle {
    font-size: 14px;
  }
  .SimplePostImgPartTextPartText {
    font-size: 12px;
  }
}
