.totalPriceBlock {
  margin-top: 16px;
  width: 100%;
  height: 50px;
  background: #ffffff;
  box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.06);
  border-radius: 2px;
  padding: 8px 25px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.totalPriceRightBlock {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 50px;
}
.totalPrice {
  font-weight: 700;
  font-size: 24px;
  line-height: 150%;
  letter-spacing: 0.02em;
  color: #000000;
}
.totalText {
  color: #003767;
  font-weight: 700;
  font-size: 24px;
  line-height: 150%;
  letter-spacing: 0.02em;
}
@media screen and (max-width: 620px) {
  .totalPriceBlock {
    flex-direction: column;
    height: auto;
    padding: 25px;
  }
  .totalPriceRightBlock {
    flex-direction: column;
    height: auto;
    gap: 10px;
  }
}
