/*attributes*/
.attributesBlock {
  width: 50%;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: space-between;
  gap: 8px;
  margin-bottom: 30px;
}
.singleAttribute {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.attributesText {
  font-weight: 400;
  font-size: 14px;
  line-height: 150%;
  color: #003767;
}
@media screen and (max-width: 1440px) {
  .attributesBlock {
    width: auto;
    max-width: 300px;
  }
}
